*, :before, :after {
  box-sizing: border-box;
}

body {
  font-family: Roboto;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body, h1, h2, h3, h4, h5, p, ul, figure {
  margin: 0;
  padding: 0;
}

ul, menu {
  margin: 0;
  list-style: none;
}

.container {
  width: 100vw;
  margin: 0 auto;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.header {
  width: 100vw;
  justify-content: center;
  display: flex;
}

.slider-container {
  height: 1104px;
  width: 100%;
  z-index: 2;
  flex-direction: column;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.slider-container__wrapper {
  width: 375px;
  height: 100%;
  padding-left: 30px;
}

.slider-container__wrap-sc {
  width: 314px;
  z-index: 5;
  position: absolute;
  top: 48px;
}

.wrap-sc__div-sc {
  flex-direction: column;
  display: flex;
}

.div-sc__nav {
  justify-content: space-between;
  display: flex;
}

.wrap-sc__div-sc__nav {
  height: auto;
  align-items: center;
  column-gap: 30px;
  display: flex;
}

.sc-nav__svg {
  fill: #fff;
}

.div-sc-wrap__information {
  flex-direction: column;
  margin-top: 54px;
  display: flex;
}

.information__info-list {
  width: 314px;
  height: 472px;
  background: rgba(0, 0, 0, .6);
  border: 1px solid #000;
  flex-direction: column;
  row-gap: 32px;
  padding-top: 35px;
  padding-left: 32px;
  display: flex;
}

.div-sc__wrap-info {
  margin-top: 45px;
  display: flex;
}

.wrap-info__title {
  width: 314px;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 8px;
  font-family: Roboto;
  font-size: 59px;
  font-weight: 900;
  line-height: 1;
}

.wrap-info__button {
  color: rgba(255, 255, 255, .8);
  background: var(--Main-Red, #dd2242);
  width: 315px;
  height: 59px;
  text-align: center;
  border: none;
  border-radius: 108px;
  font-family: DM Sans;
  font-size: 18px;
  font-weight: 700;
  line-height: 150%;
}

.info-item__svg {
  fill: #dd2242;
  margin-bottom: 14px;
}

.info-item__title {
  color: #fff;
  font-family: DM Sans;
  font-size: 26.409px;
  font-weight: 700;
  line-height: 1.5;
}

.info-item__subtitle {
  color: #939393;
  font-feature-settings: "clig" off, "liga" off;
  letter-spacing: .5px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

.slider {
  height: 1104px;
  width: 100%;
  z-index: 4;
  flex-direction: column;
  align-items: center;
  transition: transform .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.slide {
  height: 100%;
}

.video-div {
  height: 191px;
  width: 314px;
  z-index: 6;
  order: 3;
  align-items: center;
  display: flex;
  position: relative;
}

.video {
  height: 191px;
  width: 314px;
}

.video__button {
  width: 100%;
  z-index: 7;
  background: none;
  border: none;
  outline: none;
  justify-content: center;
  display: flex;
  position: absolute;
}

.img-indicator, .nav__list, .information__list {
  display: none;
}

@media screen and (min-width: 480px) {
  .tablet, .desktop {
    display: none;
  }
}

@media screen and (min-width: 768px) {
  .mobile {
    display: none;
  }

  .tablet {
    display: flex;
  }

  .desktop {
    display: none;
  }

  .slider-container {
    height: 1000px;
  }

  .slider-container__wrap-sc {
    width: 707px;
    display: flex;
  }

  .slider-container__wrapper {
    width: 768px;
  }

  .wrap-sc__div-sc {
    width: 100%;
  }

  .information__info-list {
    width: 100%;
    height: 210px;
    flex-direction: row;
    column-gap: 15px;
  }

  .video {
    width: 418px;
    height: 254px;
  }

  .video-div {
    width: 418px;
    height: 254px;
    order: 0;
  }

  .slider-container__wrap-sc {
    top: 30px;
  }

  .slider {
    height: 1000px;
  }
}

@media screen and (min-width: 1200px) {
  .div-sc__nav {
    width: 85%;
  }

  .info-list__info-item {
    width: 210px;
  }

  .information__list {
    flex-direction: column;
    row-gap: 15px;
    margin-right: 44px;
    padding-bottom: 44px;
    display: flex;
  }

  .information__link {
    color: #fff;
    text-shadow: 0 0 4px rgba(0, 0, 0, .1);
    font-family: DM Sans;
    font-size: 16px;
    font-weight: 700;
    display: flex;
    transform: rotate(-90deg);
  }

  .div-sc-wrap__information {
    width: 98%;
    height: 37%;
    flex-direction: row;
    justify-content: end;
    align-items: end;
  }

  .nav__list {
    width: 573px;
    justify-content: space-between;
    align-items: center;
    margin-left: 78px;
    display: flex;
  }

  .nav__link {
    color: #fff;
    text-transform: uppercase;
    font-family: DM Sans;
    font-size: 18px;
    font-weight: 700;
  }

  .slider-container__wrap-sc {
    height: 100%;
    width: 100%;
    display: flex;
  }

  .information__info-list {
    width: 100%;
    justify-content: space-between;
    padding-left: 71px;
  }

  .video-div {
    height: 228px;
    order: 3;
  }

  .wrap-info__button {
    width: 272px;
    height: 82px;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .div-sc__wrap-info {
    align-items: center;
  }

  .slider-container__wrapper {
    width: 100%;
  }

  .slider {
    height: 1080px;
  }

  .wrap-sc__div-sc {
    width: 100%;
  }

  .wrap-info__title {
    font-size: 120px;
    font-weight: 900;
    line-height: 1;
  }

  .tablet {
    display: none;
  }

  .desktop {
    display: flex;
  }

  .img-indicator {
    margin-right: 203px;
    display: flex;
  }

  .slider-container__wrapper {
    padding-left: 70px;
  }

  .slider-container {
    width: 100%;
  }
}

@media screen and (min-width: 1390px) {
  .information__list {
    margin-right: 198px;
  }
}

@media screen and (min-width: 1920px) {
  .slider-container {
    width: 1920px;
  }
}

.numbers {
  height: 930px;
  text-align: center;
  color: #fff;
  background-image: url("rectangle.dc80f341.png");
  background-position: center;
  background-size: cover;
  padding-bottom: 34px;
  display: flex;
}

.numbers-title {
  margin-bottom: 45px;
  padding-top: 50px;
  font-family: DM Sans;
  font-size: 50px;
}

.numbers-subtitle {
  margin-bottom: 50px;
}

.numbers p {
  font-size: 18px;
  font-style: "Paragraph Light";
}

.numbers-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.list-item:not(:last-child) {
  margin-bottom: 70px;
}

.item-title {
  color: #dd2242;
  margin-bottom: 15px;
  font-family: mobile H1;
  font-size: 45px;
}

@media screen and (min-width: 768px) {
  .numbers {
    padding-bottom: 100px;
  }

  .numbers-list {
    flex-flow: wrap;
  }

  .list-item:first-child {
    margin-right: 70px;
  }

  .numbers-title {
    padding-top: 200px;
  }
}

@media screen and (min-width: 1200px) {
  .numbers {
    height: 800px;
    text-align: center;
    color: #fff;
    background-image: url("rectangle.dc80f341.png");
    background-position: center;
    background-size: cover;
    padding-bottom: 177px;
    display: flex;
  }

  .numbers-title {
    margin-bottom: 1rem;
    padding-top: 200px;
    font-family: DM Sans;
    font-size: 50px;
    line-height: 1;
  }

  .numbers-subtitle {
    margin-bottom: 50px;
  }

  .numbers p {
    font-size: 18px;
    font-style: "Paragraph Light";
  }

  .numbers-list {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .list-item {
    margin-bottom: 70px;
    margin-right: 25px;
  }

  .item-title {
    color: #dd2242;
    margin-bottom: 40px;
  }
}

.nature {
  width: 100vw;
  margin: 0 auto;
}

.nature .nature-list {
  flex-direction: column;
  row-gap: 56px;
  padding-bottom: 40px;
  display: flex;
}

.nature .nature-list-item {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.nature .nature-img {
  width: 314px;
  height: 232px;
  border-radius: 5%;
}

.nature .backgound-mountains {
  background-image: url("background_mountains.1ccf05be.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.nature .backgound-beach {
  background-image: url("background_beach.bc95f574.png");
}

.nature .nature-title {
  color: #fff;
  margin-top: 33px;
  margin-bottom: 1px;
  font-family: DM Sans;
  font-size: 40px;
  font-weight: 700;
  line-height: 46px;
}

.nature .nature-text {
  color: var(--Text-Lighten, #e0e0e0);
  font-feature-settings: "clig" off, "liga" off;
  letter-spacing: .5px;
  margin-bottom: 40px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.nature .nature-botton {
  color: #fff;
  background: #dd2242;
  border-radius: 32px;
  padding: 9px 108px;
}

@media screen and (min-width: 768px) {
  .nature .nature-list {
    row-gap: 192px;
  }

  .nature .nature-list-item:nth-child(2) {
    padding-bottom: 305px;
  }

  .nature .nature-list-item {
    flex-direction: row;
    column-gap: 125px;
    display: flex;
  }

  .nature .nature-botton {
    height: auto;
    padding-left: 40px;
    padding-right: 40px;
  }

  .nature .nature-img {
    max-width: 630px;
    height: auto;
  }

  .nature .nature-list-item:nth-child(2) {
