@media screen and (min-width: 1200px) {
  .nature .nature-title {
    margin-top: 68px;
  }
}

.explore {
  height: 929px;
  background-image: url("field.7ac5532e.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.explore-title {
  color: var(--Blue---Background, #010a20);
  text-align: center;
  padding-top: 23px;
  padding-bottom: 23px;
  font-family: DM Sans;
  font-size: 40px;
  font-weight: 700;
  line-height: 46px;
}

.carousel > ul, .slider-list {
  display: flex;
}

.carousel-dark .carousel-indicators [data-bs-target] {
  width: 50px;
  height: 19px;
  background-color: #dd2242;
  border-radius: 15px;
}

.carousel-indicators {
  bottom: 100px;
}

.mobile-prev-icon {
  position: absolute;
  bottom: 290px;
  right: 61px;
}

.mobile-next-icon {
  position: absolute;
  bottom: 290px;
  left: 61px;
}

.desktop-prev-icon {
  display: none;
  position: absolute;
  bottom: 800px;
  right: 116px;
}

.desktop-next-icon {
  display: none;
  position: absolute;
  bottom: 800px;
  left: 116px;
}

.carousel-control-prev, .carousel-control-next {
  fill: #010a20;
}

.desktop-slider {
  display: none;
}

@media screen and (min-width: 768px) {
  .explore-title {
    padding-top: 127px;
    padding-bottom: 45px;
  }

  .mobile-slider {
    display: none;
  }

  .desktop-slider {
    display: flex;
  }

  .carousel-indicators {
    bottom: 512px;
  }

  .desktop-prev-icon {
    display: block;
    position: absolute;
    bottom: 630px;
    right: 116px;
  }

  .desktop-next-icon {
    display: block;
    position: absolute;
    bottom: 630px;
    left: 116px;
  }
}

.section__photo-gallery {
  width: 100%;
  margin-top: 32px;
  margin-bottom: 100px;
}

.photo-gallery__gallery {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.gallery__title {
  color: var(--Blue---Background, #010a20);
  text-align: center;
  margin-bottom: 20px;
  font-family: DM Sans;
  font-size: 40px;
  font-weight: 700;
  line-height: 1.1;
}

.gallery__subtitle {
  text-align: center;
  color: var(--Paragraph-Dark, #6f7787);
  text-align: center;
  font-feature-settings: "clig" off, "liga" off;
  letter-spacing: .5px;
  margin-bottom: 20px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.gallery__container {
  width: 315px;
  height: 480px;
  grid-template: "nums-6-4-2 nums-5-3-1" 1fr
                 "nums-6-4-2 nums-5-3-1" 1fr
                 "nums-6-4-2 nums-5-3-1" 1fr
                 / 1fr 1fr;
  grid-auto-flow: row;
  gap: 0 13px;
  display: grid;
}

.gallery__nums-6-4-2 {
  grid-template: "num-6 num-6 num-6" 1fr
                 "num-6 num-6 num-6" 1fr
                 "num-6 num-6 num-6" 1fr
                 "num-4 num-4 num-4" 1fr
                 "num-2 num-2 num-2" 1fr
                 "num-2 num-2 num-2" 1fr
                 / 1fr 1fr 1fr;
  grid-area: nums-6-4-2;
  grid-auto-flow: row;
  gap: 12px 0;
  display: grid;
}

.gallery__num-6 {
  grid-area: num-6;
}

.gallery__num-4 {
  grid-area: num-4;
}

.gallery__num-2 {
  grid-area: num-2;
}

.gallery__nums-5-3-1 {
  grid-template: "num-5 num-5 num-5" 1fr
                 "num-3 num-3 num-3" 1fr
                 "num-3 num-3 num-3" 1fr
                 "num-3 num-3 num-3" 1fr
                 "num-1 num-1 num-1" 1fr
                 "num-1 num-1 num-1" 1fr
                 / 1fr 1fr 1fr;
  grid-area: nums-5-3-1;
  grid-auto-flow: row;
  gap: 12px 0;
  display: grid;
}

.gallery__num-5 {
  grid-area: num-5;
}

.gallery__num-3 {
  grid-area: num-3;
}

.gallery__num-1 {
  grid-area: num-1;
}

.container__gallery, .container__gallery-desk {
  display: none;
}

.cont {
  margin-top: 50px;
}

