.footer {
  background: #010a20;
  justify-content: center;
  display: flex;
  position: relative;
}

.footer__wrap {
  width: 375px;
  height: 723px;
  flex-direction: column;
  align-items: center;
  row-gap: 36px;
  margin: 0 auto;
  padding-top: 146px;
  display: flex;
}

.footer__logo {
  width: 174px;
  height: 66px;
}

.footer__list {
  flex-direction: column;
  align-items: center;
  row-gap: 36px;
  display: flex;
}

.footer__link {
  color: var(--Main-White, #fff);
  font-feature-settings: "clig" off, "liga" off;
  letter-spacing: .637px;
  font-family: DM Sans;
  font-size: 23px;
  font-weight: 700;
  line-height: 1.5;
}

.footer__socials {
  column-gap: 30px;
  display: flex;
}

.socials__svg {
  fill: #fff;
}

.footer__about {
